'use client';

import React, { useState } from 'react';

interface ContactMessage {
  id: number;
  subject: string;
  message: string;
  priority: 'low' | 'medium' | 'high';
  status: 'sent' | 'pending' | 'resolved';
  timestamp: Date;
}

const ContactPage: React.FC = () => {
  const [formData, setFormData] = useState({
    subject: '',
    message: '',
    priority: 'medium' as 'low' | 'medium' | 'high',
  });

  const [messages, setMessages] = useState<ContactMessage[]>([
    {
      id: 1,
      subject: 'Payment Gateway Issue',
      message: 'Having trouble with online payment processing. Some customers are reporting failed transactions.',
      priority: 'high',
      status: 'resolved',
      timestamp: new Date(Date.now() - 2 * 24 * 60 * 60 * 1000), // 2 days ago
    },
    {
      id: 2,
      subject: 'Menu Update Request',
      message: 'Need help updating seasonal menu items and pricing for the holiday season.',
      priority: 'medium',
      status: 'resolved',
      timestamp: new Date(Date.now() - 5 * 24 * 60 * 60 * 1000), // 5 days ago
    },
  ]);

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitMessage, setSubmitMessage] = useState('');

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    setSubmitMessage('');

    try {
      if (!formData.subject.trim() || !formData.message.trim()) {
        throw new Error('Please fill in all required fields');
      }

      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1500));

      const newMessage: ContactMessage = {
        id: Date.now(),
        subject: formData.subject,
        message: formData.message,
        priority: formData.priority,
        status: 'sent',
        timestamp: new Date(),
      };

      setMessages(prev => [newMessage, ...prev]);
      
      setFormData({
        subject: '',
        message: '',
        priority: 'medium',
      });

      setSubmitMessage('Your message has been sent successfully! Our support team will get back to you within 24 hours.');
    } catch (error) {
      setSubmitMessage(error instanceof Error ? error.message : 'An error occurred while sending your message');
    } finally {
      setIsSubmitting(false);
    }
  };

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high':
        return 'bg-red-100 text-red-800';
      case 'medium':
        return 'bg-yellow-100 text-yellow-800';
      case 'low':
        return 'bg-green-100 text-green-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'sent':
        return 'bg-blue-100 text-blue-800';
      case 'pending':
        return 'bg-yellow-100 text-yellow-800';
      case 'resolved':
        return 'bg-green-100 text-green-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const formatDate = (date: Date) => {
    return date.toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  return (
    <div className="space-y-8">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold text-gray-900">Contact Support</h1>
        <div className="text-sm text-gray-600">
          Response time: <span className="font-medium text-green-600">Within 24 hours</span>
        </div>
      </div>

      {/* Support Information Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <div className="flex items-center space-x-3 mb-3">
            <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center">
              <span className="text-blue-600 font-bold">E</span>
            </div>
            <h3 className="font-semibold text-gray-900">Email Support</h3>
          </div>
          <p className="text-sm text-gray-600 mb-2">Get help via email</p>
          <p className="text-sm font-medium text-blue-600">support@restaurant.com</p>
        </div>

        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <div className="flex items-center space-x-3 mb-3">
            <div className="w-10 h-10 bg-green-100 rounded-lg flex items-center justify-center">
              <span className="text-green-600 font-bold">P</span>
            </div>
            <h3 className="font-semibold text-gray-900">Phone Support</h3>
          </div>
          <p className="text-sm text-gray-600 mb-2">Call us directly</p>
          <p className="text-sm font-medium text-green-600">+1 (555) 123-4567</p>
        </div>

        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <div className="flex items-center space-x-3 mb-3">
            <div className="w-10 h-10 bg-purple-100 rounded-lg flex items-center justify-center">
              <span className="text-purple-600 font-bold">C</span>
            </div>
            <h3 className="font-semibold text-gray-900">Live Chat</h3>
          </div>
          <p className="text-sm text-gray-600 mb-2">Chat with our team</p>
          <button className="text-sm font-medium text-purple-600 hover:text-purple-700">
            Start Chat
          </button>
        </div>
      </div>

      {/* Contact Form */}
      <div className="bg-white p-6 rounded-lg shadow-sm border">
        <h2 className="text-xl font-semibold mb-6">Send us a Message</h2>
        
        {submitMessage && (
          <div className={`p-4 rounded-lg mb-6 ${
            submitMessage.includes('successfully') 
              ? 'bg-green-50 text-green-800 border border-green-200' 
              : 'bg-red-50 text-red-800 border border-red-200'
          }`}>
            {submitMessage}
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Subject *
              </label>
              <input
                type="text"
                name="subject"
                value={formData.subject}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                placeholder="Brief description of your issue"
                required
                disabled={isSubmitting}
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Priority
              </label>
              <select
                name="priority"
                value={formData.priority}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                disabled={isSubmitting}
              >
                <option value="low">Low - General inquiry</option>
                <option value="medium">Medium - Need assistance</option>
                <option value="high">High - Urgent issue</option>
              </select>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Message *
            </label>
            <textarea
              name="message"
              value={formData.message}
              onChange={handleInputChange}
              rows={6}
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="Please provide detailed information about your issue or question..."
              required
              disabled={isSubmitting}
            />
          </div>

          <div className="flex items-center justify-between">
            <p className="text-sm text-gray-600">
              * Required fields. We'll respond within 24 hours.
            </p>
            <button
              type="submit"
              disabled={isSubmitting}
              className={`px-6 py-2 rounded-lg font-medium transition-colors ${
                isSubmitting
                  ? 'bg-gray-400 text-white cursor-not-allowed'
                  : 'bg-blue-600 text-white hover:bg-blue-700'
              }`}
            >
              {isSubmitting ? 'Sending...' : 'Send Message'}
            </button>
          </div>
        </form>
      </div>

      {/* FAQ Section */}
      <div className="bg-white p-6 rounded-lg shadow-sm border">
        <h2 className="text-xl font-semibold mb-6">Frequently Asked Questions</h2>
        <div className="space-y-4">
          <div className="border-b border-gray-200 pb-4">
            <h3 className="font-medium text-gray-900 mb-2">How do I update my menu items?</h3>
            <p className="text-sm text-gray-600">
              Go to the Menu section in your dashboard. You can add new items, edit existing ones, or temporarily disable items by toggling their status.
            </p>
          </div>
          
          <div className="border-b border-gray-200 pb-4">
            <h3 className="font-medium text-gray-900 mb-2">How do I change my delivery settings?</h3>
            <p className="text-sm text-gray-600">
              Visit the Settings page to update your delivery radius, minimum order amount, delivery hours, and restaurant location coordinates.
            </p>
          </div>
          
          <div className="border-b border-gray-200 pb-4">
            <h3 className="font-medium text-gray-900 mb-2">What payment methods are supported?</h3>
            <p className="text-sm text-gray-600">
              We support cash on delivery, credit/debit cards, and online payments. You can view payment breakdowns in the Payments section.
            </p>
          </div>
          
          <div className="pb-4">
            <h3 className="font-medium text-gray-900 mb-2">How do I handle order cancellations?</h3>
            <p className="text-sm text-gray-600">
              In the Orders section, you can accept or reject pending orders. Once accepted, contact the customer directly for any changes or cancellations.
            </p>
          </div>
        </div>
      </div>

      {/* Message History */}
      {messages.length > 0 && (
        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <h2 className="text-xl font-semibold mb-6">Message History</h2>
          <div className="space-y-4">
            {messages.map((msg) => (
              <div key={msg.id} className="border border-gray-200 rounded-lg p-4">
                <div className="flex items-start justify-between mb-3">
                  <div>
                    <h3 className="font-medium text-gray-900">{msg.subject}</h3>
                    <p className="text-sm text-gray-600 mt-1">{formatDate(msg.timestamp)}</p>
                  </div>
                  <div className="flex items-center space-x-2">
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${getPriorityColor(msg.priority)}`}>
                      {msg.priority.charAt(0).toUpperCase() + msg.priority.slice(1)}
                    </span>
                    <span className={`px-2 py-1 text-xs font-medium rounded-full ${getStatusColor(msg.status)}`}>
                      {msg.status.charAt(0).toUpperCase() + msg.status.slice(1)}
                    </span>
                  </div>
                </div>
                <p className="text-sm text-gray-700">{msg.message}</p>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

export default ContactPage;
